.p8 {
  padding: 8px;
}

.p16 {
  padding: 16px;
}

.p32 {
  padding: 32px;
}

.ml8 {
  margin-left: 8px;
}

.text-white {
  color: color(text);
}

.text-center {
  text-align: center;
}

.img-fluid {
  width: 100%;
}

.fs0 {
  font-size: 0;
  line-height: 0;
}

.nogrow-sm {
  @media (min-width: $sm-width) {
    flex-grow: 0;
  }
}

.flex-gap {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  gap: 5px;
}

.icon-line {
  align-items: center;
  display: flex;
  justify-content: flex-start;

  .icon {
    display: inline-block;
    margin-right: 10px;
    max-width: 18px;

    path {
      fill: color(text);
    }
  }

  &.multiple {
    .bg-text {
      margin-right: 5px;

      &:last-child {
        margin: 0;
      }
    }
  }
}

.bg-text {
  align-items: center;
  background-color: color(background, text);
  border-radius: 16px;
  color: color(text);
  display: flex;
  flex-grow: 1;
  justify-content: space-between;
  margin: 0;
  padding: 8px;

  .small-text {
    color: color(text, grey);
    font-size: 14px;
    line-height: 1;
  }

  .btn {
  }
}

.img-responsive {
  width: 100%;
}

.hint {
  font-size: 12px;
  font-style: italic;
  line-height: 1;
}

@keyframes bounce {
  0% {
    transform: translateY(0%);
  }

  10% {
    transform: translateY(-15%);
  }

  20% {
    transform: translateY(0%);
  }

  25% {
    transform: translateY(-7%);
  }

  27% {
    transform: translateY(0%);
  }

  29% {
    transform: translateY(-3%);
  }

  30% {
    transform: translateY(0);
  }
}

@font-face {
  font-family: "Minecraftia";
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/Minecraftia-Regular.eot");
}
@font-face {
  font-display: swap;
  font-family: "Minecraftia";
  font-style: normal;
  font-weight: normal;
  src:
    url("../fonts/data:font/woff2;charset=utf-8;base64,")
    format("woff2"),
    url("../fonts/Minecraftia-Regular.ttf") format("truetype"),
    url("../fonts/Minecraftia-Regular.svg#Minecraftia-Regular") format("svg");
}
